___CSS_LOADER_EXPORT___.push([module.id, `.profileCheckbox {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  transition: all 0.3s ease;
  position: relative;
}
.profileCheckbox input[type=checkbox] {
  position: absolute;
  top: 10px;
  left: 10px;
  opacity: 0;
  border: 2px solid #000;
  border-radius: 2px;
  background: none;
  cursor: pointer;
  line-height: 0;
  margin: 0 0.6em 0 0;
  outline: 0;
  padding: 0 !important;
  vertical-align: text-top;
  height: 20px;
  width: 20px;
  -webkit-appearance: none;
  opacity: 1;
}
.profileCheckbox input[type=checkbox]:hover {
  opacity: 0.5;
}
.profileCheckbox input[type=checkbox]:checked {
  background-color: #3b755f;
  border: transparent;
  opacity: 1;
}
.profileCheckbox input[type=checkbox]:checked:before {
  content: "";
  position: absolute;
  right: 50%;
  top: 50%;
  width: 7px;
  height: 10px;
  z-index: 2;
  color: white;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\\f00c";
}

.profileCheckbox:hover {
  background-color: #afc6bd;
}

.switch {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 23px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 1px solid #afc6bd;
  transition: 0.4s;
  --border-size: 2px;
  box-shadow: 0px 0px 3px 3px rgba(0, 0, 0, 0.04) inset;
}

.slider:before {
  position: absolute;
  content: "";
  height: 23px;
  width: 23px;
  left: -3px;
  bottom: -1px;
  background-color: white;
}

.slider:hover:before {
  box-shadow: 0 0 0 5px rgba(175, 198, 189, 0.5);
}

input:checked + .slider {
  background-color: #3b755f;
}

input:focus + .slider {
  box-shadow: 0 0 1px #3b755f;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
  margin: 0 4/5;
  transition: all ease 0.3s;
}`, "",{"version":3,"sources":["webpack://./src/components/UI/WidgetInput/WidgetInput.scss"],"names":[],"mappings":"AAEA;EACE,kBAAA;EACA,YAAA;EACA,WAAA;EACA,yBAAA;EACA,kBAAA;AAAF;AACE;EACE,kBAAA;EACA,SAAA;EACA,UAAA;EACA,UAAA;EACA,sBAAA;EACA,kBAAA;EACA,gBAAA;EACA,eAAA;EACA,cAAA;EACA,mBAAA;EACA,UAAA;EACA,qBAAA;EACA,wBAAA;EACA,YAAA;EACA,WAAA;EACA,wBAAA;EACA,UAAA;AACJ;AACE;EACE,YAAA;AACJ;AACE;EACE,yBAAA;EACA,mBAAA;EACA,UAAA;AACJ;AACE;EACE,WAAA;EACA,kBAAA;EACA,UAAA;EACA,QAAA;EACA,UAAA;EACA,YAAA;EACA,UAAA;EACA,YAAA;EACA,kCAAA;EACA,gBAAA;EACA,gBAAA;AACJ;;AAGA;EACE,yBAAA;AAAF;;AAGA;EACE,kBAAA;EACA,qBAAA;EACA,WAAA;EACA,YAAA;AAAF;;AAGA;EACE,UAAA;EACA,QAAA;EACA,SAAA;AAAF;;AAGA;EACE,kBAAA;EACA,eAAA;EACA,MAAA;EACA,OAAA;EACA,QAAA;EACA,SAAA;EACA,yBAAA;EAEA,gBAAA;EACA,kBAAA;EAGA,qDAAA;AAAF;;AAGA;EACE,kBAAA;EACA,WAAA;EACA,YAAA;EACA,WAAA;EACA,UAAA;EACA,YAAA;EACA,uBAAA;AAAF;;AAGA;EAGE,8CAAA;AAAF;;AAGA;EACE,yBAAA;AAAF;;AAGA;EACE,2BAAA;AAAF;;AAGA;EAGE,2BAAA;AAAF;;AAGA;EACE,mBAAA;AAAF;;AAGA;EACE,kBAAA;EACA,aAAA;EACA,yBAAA;AAAF","sourcesContent":["@import url(\"https://use.fontawesome.com/releases/v5.13.0/css/all.css\");\n\n.profileCheckbox {\n  border-radius: 50%;\n  height: 40px;\n  width: 40px;\n  transition: all 0.3s ease;\n  position: relative;\n  input[type=\"checkbox\"] {\n    position: absolute;\n    top: 10px;\n    left: 10px;\n    opacity: 0;\n    border: 2px solid #000;\n    border-radius: 2px;\n    background: none;\n    cursor: pointer;\n    line-height: 0;\n    margin: 0 0.6em 0 0;\n    outline: 0;\n    padding: 0 !important;\n    vertical-align: text-top;\n    height: 20px;\n    width: 20px;\n    -webkit-appearance: none;\n    opacity: 1;\n  }\n  input[type=\"checkbox\"]:hover {\n    opacity: 0.5;\n  }\n  input[type=\"checkbox\"]:checked {\n    background-color: #3b755f;\n    border: transparent;\n    opacity: 1;\n  }\n  input[type=\"checkbox\"]:checked:before {\n    content: \"\";\n    position: absolute;\n    right: 50%;\n    top: 50%;\n    width: 7px;\n    height: 10px;\